<template>
  <div class="container">
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" ref="modal">
      <div class="modal-dialog modal-xl" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>{{ tempProduct.title }}</span>
            </h5>
            <button type="button" class="btn btn-light btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-6">
                <img class="img-fluid" :src="tempProduct.imageUrl" alt="暫無商品圖片" style="width: 450px; height: 400px;">
              </div>
              <div class="col-sm-6">
                <span class="badge bg-primary rounded-pill">{{ tempProduct.category }}</span>
                <p>商品描述：{{ tempProduct.description }}</p>
                <p>商品內容：{{ tempProduct.content }}</p>
                <div class="h5" v-if="!tempProduct.price">{{ tempProduct.origin_price }} 元</div>
                <del class="h6" v-if="tempProduct.price">原價 {{ tempProduct.origin_price }} 元</del>
                <div class="h5" v-if="tempProduct.price">現在只要 {{ tempProduct.price }} 元</div>
                <div>
                  <div class="input-group">
                    <input type="number" class="form-control" min="1" v-model.number="quantity">
                    <button type="button" class="btn btn-primary" @click="$emit('addShoppingCart', tempProduct.id, quantity)">加入購物車</button>
                  </div>
                </div>
              </div>
              <!-- col-sm-6 end -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      product: {}
    }
  },
  methods: {
    getProduct () {
      const { id } = this.$route.params
      console.log(this.$route)
      const api = `${process.env.VUE_APP_API_URL}${process.env.VUE_APP_API_PATH}/product/${id}`
      this.$http.get(api).then(res => {
        this.product = res.data.product
        console.log(this.product)
      })
    }
  },
  created () {
    this.getProduct()
  }
}
</script>

